.button {
  width: 280px;
  height: 58px;
  text-align: center;
  margin: 27px 20px 49px 20px;
  background-color: #81b3d2;


  &__press {
    font-family: "PT Sans Narrow", "Arial", sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 21px;
    line-height: 50px;
    text-decoration: none;
    color: #ffffff;
  }
}

@media (min-width: @tablet-width) {
  .button {
    width: 458px;
    height: 58;
    margin: 0 auto;
  }
}

@media (min-width: @desktop-width) {
  .button {
    width: 458px;
    height: 58;
    margin: 0 auto;
  }
}
